@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #3779f2;
  padding: 30px 0;
}

ul {
  list-style-type: none;
  padding: 0; 
  margin: 0;
}

.headline {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 72px;
  text-transform: uppercase;
  margin-bottom: 32px;
  color: #ffffff;
}

.search-form {
  margin-bottom: 44px;
  display: flex;
}

.search-input {
  margin-right: 10px;
  padding: 15px;
  flex: 1 1 70%;
  background-color: #3779f2;
  color: #fff;
  outline: none;
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-bottom: 2px solid #ffff;
}

.search-input::-webkit-input-placeholder {
  color: #fff;
  opacity: 0.8;
}

.search-input:-ms-input-placeholder {
  color: #fff;
  opacity: 0.8;
}

.search-input::-ms-input-placeholder {
  color: #fff;
  opacity: 0.8;
}

.search-input::placeholder {
  color: #fff;
  opacity: 0.8;
}

.submit-button {
  flex: 1 1 30%;
  background-color: #ffffff;
  cursor: pointer;
  color: #3779f2; 
  text-transform: uppercase;
  border: none;
  font-weight: bold;
}

.page-grid {
  padding: 0 15px;
  overflow-wrap: break-word;
  margin: 0 auto;
  max-width: 968px;
  width: 100%;
}

.item-container {
  background-color: #ffffff;
  box-shadow: 0px 0px 24px -10px rgba(0,0,0,0.75);
  padding: 30px 20px 30px;
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  max-width: 100%;
}

.item-container__title {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 32px;
  font-weight: bold; 
  line-height: 1.5em;
  margin-bottom: 20px;
}

.item-container__category {
  font-family: 'Open-sans', sans-serif;
  letter-spacing: 0.03rem;
  font-size: 18px;
  line-height: 1.75rem;
  margin-bottom: 12px;
}

.item-container__category__label {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}
